  padding: 0;
  list-style-type: none;
  position: absolute;
  right: 0;
  top: 15px;
}

.site-header__social li {
  display: inline-block;
  margin-left: 10px;
  width: 30px;
  height: 30px;
}

.site-header__social li a {
  display: block;
  height: 100%;
  opacity: .8;
}

.site-header__social li a:hover {
  opacity: 1;
}

.site-header__social li.twit a {
  background: url(../img/h-twit.png);
}

.site-header__social li.vk a {
  background: url(../img/h-vk.png);
}

.site-header__social li.fb a {
